import React, { useState } from "react"
import Layout from "../components/Layout"
import { graphql } from "gatsby"
import blogTemplateStyles from "../styles/templates/blog.module.scss"
//this component handles the blur img & fade-ins
import Img from 'gatsby-image'
import CryptoJS from 'crypto-js'
import {
  getSessionPassword,
  getQueryPassword,
  deleteSessionCookie
} from '../helpers/passwordProtectUtils.js'
import PasswordProtect from "../components/PasswordProtect"
import BlogList from "../components/BlogList"

export default function Blog(props) {
  const data = props.data.markdownRemark
  const [decrypted, setDecrypted] = useState('');

  // Check for and decrypt any password protected content
  if (data.fields.encrypted && ! decrypted) {
    try {
      const passwordCandidate = getQueryPassword(window.location) || getSessionPassword();
      setDecrypted(CryptoJS.AES.decrypt(data.html, passwordCandidate).toString(CryptoJS.enc.Utf8));
    } catch(e) {
      // Remove cookie if set
      deleteSessionCookie();
      // Return the password protect module component if unable to get password or decrypt content
      return (
        <PasswordProtect />
      )
    }
  }

  return (
    <Layout page={data.fields.slug} slug={data.fields.slug}>
      <article className={blogTemplateStyles.blog}>
        <figure className={blogTemplateStyles.blog__hero}>
          <Img
            fluid={data.frontmatter.hero_image.childImageSharp.fluid}
            alt={data.frontmatter.title}
          />
        </figure>
        <div className={blogTemplateStyles.blog__info}>
          <h1>{data.frontmatter.title}</h1>
          <h3>{data.frontmatter.date}</h3>
        </div>
        <div
          className={blogTemplateStyles.blog__body}
          dangerouslySetInnerHTML={{ __html: decrypted ? decrypted : data.html }}
        ></div>
        <div className={blogTemplateStyles.blog__footer}>
          <h2>
            See more projects:
          </h2>
        </div>
      </article>
      <section>
        <BlogList curPost={data.fields.slug}/>
      </section>
    </Layout>
  )
}

//dynamic page query, must occur within each post context
//$slug is made available by context from createPages call in gatsby-node.js
export const getPostData = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      fields {
        slug
        encrypted
      }
      frontmatter {
        title
        date(formatString: "MMMM Do, YYYY")
        hero_image {
          childImageSharp {
            fluid(maxWidth: 1500) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
      html
    }
  }
`
